<template>
  <div class="main">
	   <div v-html="state.headerText"></div>
     <textarea spellcheck="false" ref="scriptInput" id="scriptInput" @input="processInput()"></textarea>
		 <div id="debug" ref="debug"></div>
  </div>
</template>

<script>
export default {
  name: 'Main',
  props: [ 'state' ],
	data(){
		return {
			goodClass: 'goodClass',
			badClass: 'badClass'
		}
	},
	methods:{
		redraw(oldval, newval){
			let oldTokens = this.tokenize(oldval)
			let newTokens = this.tokenize(newval)
			let out = ''
			let ret = '', l1, l2
       this.$refs['debug'].innerHTML = ''
      for(let i=0; i<oldval.length; i++){
				let v = oldval[i]
				if((l1=oldTokens.filter(q=>q.pos == i)).length){
					l2 = newTokens.filter(q=>q.pos == i)
					if(l2.length){
            ret = '<span class="'
				    ret += l1[0].value === l2[0].value ? this.goodClass : this.badClass
            ret += '">' + l1[0].value + '</span>'
				  	i += Math.max(0, l1[0].value.length-1)
				  }else{
            switch(v){
							case "\n": ret = "<br>"; i; break
							//case ""
							default: ret = v; break
						}
					}
				}else{
          switch(v){
            case "\n": ret = "<br>"; i; break
            //case ""
            default: ret = v; break
          }
				}
				out += ret
			}
      this.$refs['debug'].innerHTML = out
		},
		getCorrection(){
      if(!this.state.scriptInput.value){
        this.$refs['debug'].innerHTML = ''
        return
      }
      let sendData = {tokens: this.state.scriptInput.value}
      fetch('https://'+this.state.rootDomain+'/autosyntax/autocorrect.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(sendData),
      }).then(res=>res.text()).then(data=>{
        if(data){
					//data[1] = data[1]//.split("\n").join('<br>').split(' ').join('&nbsp;')
          this.redraw(sendData.tokens, data)
        } else {
					this.redraw('', '')
				}
      })
		},
		tokenize(str){
      if(!str) return
		  let tokens = []
			let token = {pos: 0, value: ''}
      str.split('').map((v, i)=>{
         if((/[a-zA-Z]/).test(v) || (/[0-9]/).test(v) || v == '_' || v == '-'){
           token.value  += v
         }else{
           tokens=[...tokens, token]
           token = {pos: i+1, value: ''}
         }
      })
      if(token) tokens=[...tokens, token]
			return tokens
		},
		processInput(){
      this.$refs['debug'].innerHTML=''
			let script = this.state.scriptInput.value
			let tokens = this.tokenize(script)
			if(tokens.length){
				this.state.tokens = tokens.map(v=>{
				  return {
					  token: v,
				  	stale: true,
		    		correction: '',
		  		}
				})
			} else {
				this.state.tokens = []
				this.tokens = []
				this.$refs['debug'].innerHTML=''
			}
			this.getCorrection()
		}
	},
	mounted(){
		this.state.scriptInput = this.$refs['scriptInput']
		this.state.scriptInput.focus()
	}
}
</script>

<style>
.main{
  margin: 10px;
	padding: 10px;
	border: 1px solid #4f86;
}
#scriptInput{
  background: #333;
	color: #ff0;
  width: calc(100% - 5px);
	height: calc(100vh - 70px);
}
#scriptInput:focus{
  outline: none;
}
#debug{
  position: absolute;
	top: 50%;
	width: 100%;
	height: calc(50% - 12px);
	border: 1px solid red;
}
.goodClass{
  background: #4f86;
}
.badClass{
	background: #f486;
}
</style>
